import React from 'react';

import Content from './Content';

class ContentCaterpillars extends React.Component {

    // props, or properties
    // let us pass information back and forth between
    // different levels of component


    
    render() {
        return (
            <Content 
                source = "https://cdn.pixabay.com/photo/2016/02/19/11/38/caterpillar-1209834_1280.jpg"
                labelText = "Caterpillars (Lepidoptera)"
                bodyText = "Caterpillars even better!"
            />
        )
    }


}

export default ContentCaterpillars;