import React from 'react';

import Content from './Content';

class ContentLadybugs extends React.Component {

    // props, or properties
    // let us pass information back and forth between
    // different levels of component


    
    render() {
        return (
            <Content 
                source = "https://cdn.pixabay.com/photo/2016/06/26/08/27/ladybug-1480102_1280.jpg"
                labelText = "Ladybugs (Coleoptera)"
                bodyText = "Ladybugs are vibrant and beautiful!"
            />
        )
    }


}

export default ContentLadybugs;