import React from 'react';

import ContentLabel from './ContentLabel';
import './Content.css';


class Content extends React.Component {

    // props, or properties
    // let us pass information back and forth between
    // different levels of component

    render() {
        let additionalInfo = this.props.additionalInfo;
        let additionalContainer;

        // fill in the additional container IF we have additional information
        // to add to the DOM
        if (additionalInfo) {
            additionalContainer =   <div class="content__additional">
                                        <p>Check out this additional information:</p>
                                        {additionalInfo}
                                    </div>
        }
        else {
        
        }

        return (
            <main className="content">
                <img className="content__image" src={this.props.source} alt="Image of insect" />
                
                <ContentLabel
                    labelText = {this.props.labelText} 
                />
                 
                <p className="content__body">
                   {this.props.bodyText}
                </p>

                
                {additionalContainer} 
            </main>
        )
    }


}

export default Content;