import React from 'react';

import BeeLogo from './media/images/bee_logo.png';

import './Header.css';

class Header extends React.Component {

    //  props??
    
    // render: decides what is displayed on the page
    // when this component is used
    render() {
        return (
            <header className="header">
                <h1 className="header__title">Insectopedia</h1>
                <img className="header__logo" alt="Site logo of bee" src={BeeLogo} />
            </header>
        )
    }


}

export default Header;