import React from 'react';

import Content from './Content';

class ContentBees extends React.Component {

    // props, or properties
    // let us pass information back and forth between
    // different levels of component


    
    render() {
        return (
            <Content 
                source = "https://cdn.pixabay.com/photo/2016/08/06/22/07/bee-1575239_1280.jpg"
                labelText = "Bees (Apis)"
                bodyText = "Bees are super dee duper!"

                additionalInfo = "Did you know that bees make honey?"
                additionalImage = "https://cdn.pixabay.com/photo/2014/05/23/18/15/honey-352205_1280.jpg"
            />
        )
    }


}

export default ContentBees;